	module.exports = {"data":{"markdownRemark":{"headings":[],"html":"<p>![Brand identity for the Path to the Podium board game.](../assets/screen shot 2018-01-21 at 17.06.07.png)</p>\n<p>![Sample of the card design.](../assets/screen shot 2018-01-21 at 17.07.38.png)</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 100%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Cut tests of the character cards.\"\n        title=\"\"\n        src=\"/the-next-website/static/img_7379-45955bb94d1ee6a2668b471aaad4e6eb-755ae.jpg\"\n        srcset=\"/the-next-website/static/img_7379-45955bb94d1ee6a2668b471aaad4e6eb-f07fc.jpg 125w,\n/the-next-website/static/img_7379-45955bb94d1ee6a2668b471aaad4e6eb-9d06f.jpg 250w,\n/the-next-website/static/img_7379-45955bb94d1ee6a2668b471aaad4e6eb-755ae.jpg 500w,\n/the-next-website/static/img_7379-45955bb94d1ee6a2668b471aaad4e6eb-63ca3.jpg 750w,\n/the-next-website/static/img_7379-45955bb94d1ee6a2668b471aaad4e6eb-9b9a3.jpg 1000w,\n/the-next-website/static/img_7379-45955bb94d1ee6a2668b471aaad4e6eb-6e031.jpg 1500w,\n/the-next-website/static/img_7379-45955bb94d1ee6a2668b471aaad4e6eb-3e1dc.jpg 3024w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>Path to the Podium is a satirical Board game based on real-life events and challenges that athletes around the world face in their careers. Players play as a celebrated athlete of a minority background, and encounter trials along their way to reach the top. By drawing instanced situations and making their way through them, players can learn more about the social institution of Sport. The goal of this Baord game is to shed light on issues that underprivileged communities (even famous athletes) go through, and to encourage and inspire participation in unconventional activities such as fencing and boxing.</p>","frontmatter":{"title":"Path to the Podium","graduate":"My Van Loc","category":"Package Design","category2":"Information Design","category3":"Experiential Design","image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/the-next-website/static/loc_myvan_3_digital_002-c678c777c8f5e4b696df5d4d412c9e2d-a7a62.jpg","srcSet":"/the-next-website/static/loc_myvan_3_digital_002-c678c777c8f5e4b696df5d4d412c9e2d-5a1a9.jpg 375w,\n/the-next-website/static/loc_myvan_3_digital_002-c678c777c8f5e4b696df5d4d412c9e2d-0908c.jpg 750w,\n/the-next-website/static/loc_myvan_3_digital_002-c678c777c8f5e4b696df5d4d412c9e2d-a7a62.jpg 1500w,\n/the-next-website/static/loc_myvan_3_digital_002-c678c777c8f5e4b696df5d4d412c9e2d-3028f.jpg 2004w","sizes":"(max-width: 1500px) 100vw, 1500px"}}}}}},"pathContext":{"slug":"/work/path-to-the-podium/","name":"Path to the Podium"}}

